<template>
	<div class="QRcodeModal" v-if="isOpen">
		<qrcode-vue :value="walletConnectionURL" v-if="walletConnectionURL"  size="300" level="H" background="transparent" render-as="svg"></qrcode-vue>
		<div class="btn" @click="close">Close</div>
		<div class="btn" @click="goToWallet">Go to wallet</div>
	</div>
</template>

<script>
import {connector} from '@/connector.js'
import QrcodeVue from 'qrcode.vue'

export default {
	name: 'QRcodeModal',
  props: ['walletInfo', 'isOpen'],
  components: { QrcodeVue },
	data() {
		return {
			walletConnectionURL: '',
		}
	},	
	methods: {
		// Создаем QR code
		setWalletConnectionURL(){
			if (this.walletInfo) {
				this.walletConnectionURL = connector.connect({ bridgeUrl: this.walletInfo.bridgeUrl, universalLink: this.walletInfo.universalLink });
			}
		},
		close(){
			this.$emit('onClose')
		},
		goToWallet(){
			window.open(this.walletInfo.aboutUrl, '_blank');
		}
	},
	watch: {
		walletInfo() {
			this.setWalletConnectionURL()
		}
	}
}
</script>
