<template>
  <div class="ConnectWalletModal">
		<h1>{{ message }}</h1>
		<h3>walletsList</h3>
		<div class="btn-group">
			<div class="btn" v-for="walletInfo in walletsList" :key="walletInfo.name" @click="onWalletClick(walletInfo)">
				<img :src="walletInfo.imageUrl" alt="" height="10">
				<span>{{ walletInfo.name }}</span>
			</div>
		</div>

		<QRcodeModal
			:isOpen="!!selectedSelectedWalletInfo"
			:onClose="false"
			:walletInfo="selectedSelectedWalletInfo"
			v-on:onClose="selectedSelectedWalletInfo = null"
		/>

  </div>
</template>

<script>
import { connector } from '@/connector.js'
import { isWalletInfoCurrentlyInjected, isWalletInfoRemote } from '@tonconnect/sdk';
import QRcodeModal from './QRcodeModal.vue';

export default {
	name: 'ConnectWalletModal',
  components: { QRcodeModal },
	data() {
		return {
			message: '',
			walletsList: null,
			selectedSelectedWalletInfo: null
		}
	},
	methods: {
		async getWallets() {
			this.walletsList = await connector.getWallets(); // получаем список всех кошельков
		},
		setSelectedWalletInfo(walletInfo) {
			this.selectedSelectedWalletInfo = walletInfo;
		},
		onWalletClick(walletInfo) {
			// проверяем поддерживае ли кошелек bridge
			if (isWalletInfoRemote(walletInfo)) {
				this.message = 'isWalletInfoRemote'
				return this.setSelectedWalletInfo(walletInfo);
			}

			// про веряем является ли кошелек Injected и внедре ли он сейчас на страницу
			if (isWalletInfoCurrentlyInjected(walletInfo)) {
				this.message = 'isWalletInfoCurrentlyInjected'
				return connector.connect({ jsBridgeKey: walletInfo.jsBridgeKey });
			}

			// иначе просто кидаем на сайт для скачивания
			// window.open(walletInfo.aboutUrl, '_blank');
		}
	},
	mounted() {
		this.getWallets()
	}
}
</script>

