<template>
  <div id="app">
		{{ slicedUserFriendlyAddress }}
		<div class="btn" @click="disconnectWallet" v-if="wallet">Disconnect</div>
		<hr>
		<ConnectWalletModal v-if="!wallet"/>
  </div>
</template>

<script>
import ConnectWalletModal from '@/components/ConnectWalletModal.vue'
// import { useWallet } from "./hooks/useWallet";
import {connector} from "./connector";
import {CHAIN, toUserFriendlyAddress} from "@tonconnect/sdk";

export default {
	name: 'App',
	components: {
		ConnectWalletModal
	},
	data(){
		return {
			wallet: null,
		}
	},
	methods: {
		disconnectWallet() {
			connector.disconnect()
		},
		async test() {
			await connector.onStatusChange(
				walletInfo =>
				{
					this.wallet = walletInfo;
				}
			);
		},
	},
	computed: {
		slicedUserFriendlyAddress() {
			if (this.wallet) {
				// toUserFriendlyAddress принимает первым аргументом адрес а вторым тестнет ли
				let userFriendlyAddress =this.wallet ? toUserFriendlyAddress(this.wallet.account.address, this.wallet.account.chain === CHAIN.TESTNET) : '';
				return userFriendlyAddress.slice(0, 4) + '…' + userFriendlyAddress.slice(-4);
			} else {
				return null
			}
		}
	},
	mounted() {
		this.test()
		// if (this.wallet) {
		// 	this.userFriendlyAddress =this.wallet ? toUserFriendlyAddress(this.wallet.account.address, this.wallet.account.chain === CHAIN.TESTNET) : '';
		// 	this.slicedUserFriendlyAddress = this.userFriendlyAddress.slice(0, 4) + '…' + this.userFriendlyAddress.slice(-4);
		// }
	}
}

</script>

